<template>
  <div class="password-prompt">
    <div class="password-container">
      <h2></h2>
      <el-input
        type="password"
        v-model="password"
        placeholder=""
        @keyup.enter="checkPassword"
      ></el-input>
      <el-button type="primary" @click="checkPassword" style="margin-top: 20px"></el-button>
      <p class="error-message" v-if="errorMsg">{{ errorMsg }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PasswordPrompt',
  data() {
    return {
      password: '',
      errorMsg: ''
    }
  },
  methods: {
    checkPassword() {
      // 
      const validPasswords = ['66'];
      
      if (validPasswords.includes(this.password)) {
        // 
        localStorage.setItem('passwordVerified', 'true');
        // 
        window.location.reload();
      } else {
        this.errorMsg = '';
        setTimeout(() => {
          this.errorMsg = '';
        }, 2000);
      }
    }
  }
}
</script>

<style scoped>
.password-prompt {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.password-container {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
}

.error-message {
  color: red;
  font-size: 12px;
  margin-top: 10px;
}
</style>