<template>
  <div class="filter-panel el-card" style="position: relative">
    <!--  -->
    <div class="el-form-item">
      <label class="el-form-item__label"></label>
      <el-select
        v-model="filter.chapter"
        multiple
        filterable
        allow-create
        placeholder=""
        @change="applyFilter"
      >
        <el-option
          v-for="chapter in chapters"
          :key="chapter"
          :label="chapter"
          :value="chapter"
        ></el-option>
      </el-select>
      <el-button @click="selectAll('chapter')" size="small"></el-button>
      <el-button @click="clearSelection('chapter')" size="small"
        ></el-button
      >
    </div>
    <!--  -->
    <div class="el-form-item">
      <label class="el-form-item__label"></label>
      <el-select
        v-model="filter.level"
        multiple
        filterable
        allow-create
        placeholder=""
        @change="applyFilter"
      >
        <el-option
          v-for="level in levels"
          :key="level"
          :label="level"
          :value="level"
        ></el-option>
      </el-select>
      <el-button @click="selectAll('level')" size="small"></el-button>
      <el-button @click="clearSelection('level')" size="small"></el-button>
    </div>
    <!--  -->
    <div class="mode-switcher-container">
      <ModeSwitcher
        class="mode-switcher-original"
        @mode-changed="$emit('mode-changed', $event)"
      />
    </div>
  </div>
</template>

<script>
import ModeSwitcher from "./ModeSwitcher.vue";

export default {
  name: "FilterPanel",
  components: {
    ModeSwitcher,
  },
  props: ["initialFilter", "chapters", "levels"],
  data() {
    return {
      filter: { chapter: [], level: [] },
    };
  },
  methods: {
    applyFilter() {
      const chapter = this.filter.chapter.includes("all")
        ? null
        : this.filter.chapter.length > 0
        ? this.filter.chapter.map(Number)
        : null;
      const level = this.filter.level.includes("all")
        ? null
        : this.filter.level.length > 0
        ? this.filter.level.map(Number)
        : null;
      console.log(":", { chapter, level });
      this.$emit("filter-changed", { chapter, level });
    },
    selectAll(type) {
      if (type === "chapter") {
        this.filter.chapter = [...this.chapters].map(Number);
      } else {
        this.filter.level = [...this.levels].map(Number);
      }
      this.applyFilter();
    },
    clearSelection(type) {
      if (type === "chapter") {
        this.filter.chapter = [];
      } else {
        this.filter.level = [];
      }
      this.applyFilter();
    },
    reset() {
      this.filter = { chapter: [], level: [] };
      this.$emit("reset");
    },
  },
};
</script>

