.filter-panel {
  padding: 20px 20px 0 20px;
  position: relative; /*  */
}
.el-form-item {
  margin-bottom: 22px;
  display: block;
  margin-right: 20px;
}
.el-select {
  width: 300px;
}
.el-button {
  margin-left: 10px;
}
.mode-switcher-container {
  position: absolute;
  top: 20px; /*  padding  */
  right: 20px; /*  20px */
}
</style>
