<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
// 

export default {
  name: 'App',
  mounted() {
    // 
    if (process.env.NODE_ENV === 'production') {
      const isVerified = localStorage.getItem('passwordVerified') === 'true';
      if (!isVerified && this.$route.path !== '/password') {
        this.$router.push('/password');
      }
    }
  }
}
</script>
